<template>
    <div class="mainbox">
        <span class="el-dropdown-link">
            &emsp;角色状态<i class="el-icon-caret-bottom" style="margin-left: 55px;"></i>
        </span>
        <el-input v-model="input" class="inputs" @input="searchfun()" placeholder="请输入角色名称进行模糊搜索"
            prefix-icon="el-icon-search" style="width: 400px;margin-left: 15px;"></el-input>
        <el-button @click="drawer = true"
            style="background-color: #409EFF;border-radius: 0px; height: 35px;line-height:3px;color: white; float: right;margin-right: 12px;margin-top: 12px"><i
                class="el-icon-plus"></i>添加系统角色</el-button>
        <el-drawer title="我是标题" :visible.sync="drawer" :with-header="false">
            <div>
                <div class="logo">欢迎来到权限管理添加系统</div>
                <el-input placeholder="请输入角色名称" style="margin-top: 30px;" v-model="obj.rolename"></el-input>
                <br><br>
                <el-input placeholder="请输入角色描述" v-model="obj.describe"></el-input>
                <br><br>
                <!-- <el-input placeholder="请输入角色权限配置" v-model="obj.powers"></el-input> -->
                <span
                    style="font-size: 18px;font-weight: 600;text-align: center;width: 450px;display: block">权限配置</span>
                <el-tree :data="data" v-model="obj.powers" show-checkbox default-expand-all node-key="id" ref="tree"
                    highlight-current :props="defaultProps">
                </el-tree>
                <br><br>
                <span
                    style="font-size: 18px;font-weight: 600;text-align: center;width: 450px;display: block">请输入角色状态是否开启</span>
                <div style="width: 450px;text-align: center; margin-top: 12px">
                    <el-radio v-model="obj.status" label="1">启用</el-radio>
                    <el-radio v-model="obj.status" label="0">禁用</el-radio>
                </div>
                <br>
                <el-button style="margin-left: 45%;margin-top: 5%" @click="sub">提交</el-button>
            </div>
        </el-drawer>
        <div class="footbox" style="width: 100%;">
            <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark"
                style="width: 100%;background-color: #ECEEF5;" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="80">
                </el-table-column>
                <el-table-column prop="name" label="角色名称" width="190">
                    <template slot-scope="scope">{{ scope.row.rolename }}</template>
                </el-table-column>
                <el-table-column prop="describe" label="角色描述" width="190">
                    <template slot-scope="scope">{{ scope.row.describe }}</template>
                </el-table-column>
                <el-table-column prop="powers" label="角色权限配置" width="190">
                    <template slot-scope="scope">{{ getNamesByIds(shujulist, scope.row.powers) }}</template>
                </el-table-column>
                <el-table-column prop="status" label="角色启用状态" width="190">
                    <template slot-scope="scope">
                        {{ scope.row.status ? "启用中" : "已停用" }}
                        <div :class="scope.row.status == 1 ? 'green' : 'red'"
                            style="width: 7px;height: 7px;border-radius: 50%;display: inline-block;margin-bottom: 2px;">
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="lasttime" label="最后编辑时间" width="190">
                    <template slot-scope="scope">{{ formatTimestamp(scope.row.lasttime) }}</template>
                </el-table-column>
                <el-table-column prop="name" label="操作" width="218">
                    <template slot-scope="scope">
                        <!-- 编辑 -->
                        <el-button type="text" size="small" @click="handleEdit(scope.row.id)"><i
                                style="color: #D2D7DC;font-size:18px;" class="el-icon-edit"></i></el-button>
                        <!-- 删除 -->
                        <el-button type="text" size="small" @click="handleDelete(scope.row.id)"><i
                                style="color: #D2D7DC;font-size:18px;" class="el-icon-delete"></i></el-button>
                        <!-- 启用状态 -->
                        <el-button type="text"  style="color: #D2D7DC;font-size: 19px"
                            :icon="scope.row.status === 0 ? 'el-icon-video-play' : 'el-icon-switch-button'"
                            @click="xiugai(scope.$index, scope.row)">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination style="text-align: center;margin-top: 12px;" background layout="prev, pager, next"
                :total="total" @current-change=def>
            </el-pagination>
        </div>
    </div>
</template>
<script>
import { getquanxianlist, getquanxiansearches, delquanxian, addquanxian, editquanxian, getquanxianbyid, getrolelist, updatequanxianstatus } from "../../api/html.js"
export default {
    data() {
        return {
            data: [{
                id: 1,
                label: '首页',
                children: [{
                    id: 4,
                    label: '企业管理',
                }],
            }, {
                id: 2,
                label: '通话统计',
                children: [{
                    id: 5,
                    label: '并发统计'
                }]
            }, {
                id: 3,
                label: '权限管理',
                children: [{
                    id: 7,
                    label: '系统角色管理'
                }, {
                    id: 8,
                    label: '系统用户管理'
                }]
            }],
            defaultProps: {
                children: 'children',
                label: 'label'
            },
            total: 0,
            page: 0,
            drawer: false,
            input: "",
            tableData: [],
            // 树形控件存数据
            getrolelists: [],
            // 创建一个存储转系统角色权限配置列表的数组
            shujulist: [],
            multipleSelection: [],
            cid: "",
            listid: "",
            obj: {
                rolename: "",
                describe: "",
                powers: "",
                status: "",
            },
        }
    },
    created() {
        this.show()
        this.getrolelistss()
    },

    methods: {
        getrolelistss() {
            getrolelist().then(res => {
                console.log(res.data.data);
                this.shujulist = res.data.data
            })
        },
        // 修改状态盒子背景颜色
        xiugai(index, row) {
            console.log(index, row);
            // 通过接口进行获取id和状态
            updatequanxianstatus({
                id: row.id,
                status: row.status ? 0 : 1
            }).then(res => {
                // 重新获取数据进行刷新
                this.show()
                console.log(res);
            })
        },
        // 转换日期格式
        formatTimestamp(timestampInSeconds) {
            // 确保时间戳是以毫秒为单位
            const timestampInMilliseconds = timestampInSeconds * 1000;
            const date = new Date(timestampInMilliseconds);
            const formattedTime = `${date.getFullYear()}-${date.getMonth() + 1
                }-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
            return formattedTime;
        },
        // 分页
        def(pages) {
            console.log(pages);
            this.show(pages)
        },
        // 模糊搜索
        searchfun() {
            let obj = { rolename: this.input }
            getquanxiansearches(obj).then(res => {
                this.tableData = res.data.data
            })
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        show(page) {
            getquanxianlist(page).then(res => {
                console.log(res);
                // 渲染
                this.tableData = res.data.data
                this.total = res.data.pagecount
            })
        },
        // 删除
        handleDelete(id) {
            const isConfirmed = confirm('确定要删除这条记录吗？');
            if (isConfirmed) {
                delquanxian(id).then(res => {
                    console.log(res);
                    this.show();
                })
            }
        },

        // 全部信息列表
        getlist() {
            getrolelist().then(res => {
                this.getrolelists = res.data.data
            })
        },
        getNamesByIds(data, idsStr) {
            // 将输入的字符串转换为整数数组
            const ids = idsStr.split(',').map(id => parseInt(id.trim()));
            // 用来存储匹配到的name值
            const matchedNames = [];
            // 遍历原始数据数组
            data.forEach(item => {
                // 如果当前项的id在ids数组中，就添加它的name到matchedNames
                if (ids.includes(item.id)) {
                    matchedNames.push(item.name);
                }
            });
            // 最后将matchedNames数组转换为以逗号分隔的字符串并返回
            return matchedNames.join(', ');
        },
        // 添加
        sub() {
            // 将scope.row.powers转为字符串
            let str = this.$refs.tree.getCheckedKeys().join(",")
            this.obj.powers = str
            console.log(this.obj.powers);
            // 根据id进行判断是否添加和删除 cid存贮编辑时获取的单个id进行获取数据进行回显
            if (this.id == null && this.cid == "") {
                let add = {
                    rolename: this.obj.rolename,
                    describe: this.obj.describe,
                    powers: this.obj.powers,
                    status: this.obj.status,
                }
                addquanxian(add).then(res => {
                    console.log(res);
                    this.show()
                    // 清空
                    this.obj = {
                        rolename: "",
                        describe: "",
                        powers: "",
                        status: "",
                    }
                }).catch(error => {
                    console.error("添加失败:", error);

                });
            } else {
                // 编辑
                let edit = {
                    id: this.cid,
                    rolename: this.obj.rolename,
                    describe: this.obj.describe,
                    powers: this.obj.powers,
                    status: this.obj.status,
                }
                editquanxian(edit).then(res => {
                    console.log(res);
                    this.show()
                    // 清空
                    this.obj = {
                        rolename: "",
                        describe: "",
                        powers: "",
                        status: "",
                    }
                    this.cid = ""
                    this.show()
                }).catch(error => {
                    console.error("修改失败:", error);
                })
            }
        },

        // 编辑的点击事件
        handleEdit(id) {
            console.log(id);
            // 创建一个储存id的cid 将id储存进去
            this.cid = id;
            getquanxianbyid(this.cid).then(res => {
                console.log(res);
                // 回显
                this.obj.rolename = res.data.rolename;
                this.obj.describe = res.data.describe;
                this.obj.powers = res.data.powers;
                this.obj.status = res.data.status;
            })
            this.drawer = true;
        },
        // 权限管理配置列表
        getlast(id) {
            console.log(id);
            this.listid = id
            getrolelist(this.listid).then(res => {
                console.log(res);
            })

        }
    }
}
</script>
<style scoped>
.red {
    background-color: red
}

.green {
    background-color: green
}

.logo {
    width: 400px;
    height: 53px;
    margin: auto;
    font-weight: 600;
    font-size: 30px;
    text-align: center;
    line-height: 73px;
}

.footbox {
    margin-top: 12px;
}

.mainbox {
    width: 98%;
    height: 100%;
    background-color: white;
    margin: auto;
    margin-top: 12px;
    margin-bottom: 12px;
}

.el-dropdown-link {
    cursor: pointer;
    color: #3EA6FE;
    display: inline-block;
    font-weight: 600;
    width: 160px;
    height: 35px;
    line-height: 35px;
    margin-left: 11px;
    background-color: #E9F3FB;
    margin-top: 12px;
}

.inputs {
    display: inline-block;
}
</style>